/* Destroyed ship cell */
.cell-red {
   position: absolute;
   z-index: 1;
   top: 0;
   left: 0;
   width: 41px;
   height: 41px;
   display: flex;
   justify-content: center;
   align-items: center;
}
.cell-red::before,
.cell-red::after {
   position: absolute;
   content: "";
   width: 100%;
   height: 4px;
   background-color: #ff7a7a;
   border-radius: 10px;
}

.cell-red::before {
   transform: rotate(45deg);
}

.cell-red::after {
   transform: rotate(-45deg);
}

/* Missed shot and ship bounds */
.cell-missed,
.cell-bounds {
   display: flex;
   justify-content: center;
   align-items: center;
   position: absolute;
   top: 0;
   left: 0;
   z-index: 1;
   width: 40px;
   height: 40px;
   background: rgba(236, 236, 236, 0.5);
}

.cell-circle {
   width: 5px;
   height: 5px;
   border-radius: 50%;
}

.cell-missed > .cell-circle {
   background: rgba(13, 92, 0, 0.8);
}
.cell-bounds > .cell-circle {
   background: rgba(77, 148, 82, 0.2);
}
